import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { DomSanitizer } from '@angular/platform-browser';
import { Category, Ingredient, ReceitasWrapper, Recipe, Step } from '../../models/receitas-meyry.model';
import { RecipeService } from '../../services/recipes.service';


@Component({
    selector: 'app-cadastro-receita',
    templateUrl: 'cadastro-receita.component.html',
    styleUrls: ['cadastro-receita.component.scss']
})
export class CadastroReceitaComponent implements OnInit {
    form:FormGroup;
    selectedFile;


    constructor(
        private formBuilder: FormBuilder,
        private domSanitizer: DomSanitizer,
        private service: RecipeService
    ){}

    ngOnInit(): void {
        this.form = this.formBuilder.group({
            name: [''],
            description: [''],
            prepTime: [''],
            serving: [''],
            image: ['']            
        });

        this.getRecipe();
    }

    public salvar() {
        let file = this.selectedFile;
        
        this.service.saveImageRecipe(file).subscribe(
            (dados) => {
                console.log(dados);
            },
            (e) => {
                console.log(e);
            }
        );
    }

    public selectFile(event) {        
        // this.selectedFile = event.target.files[0];

        const file = event.target.files[0];
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => {
            this.selectedFile = reader.result;
        };

    }
myUrl;
    public getRecipe() {
        this.service.getImageRecipe().subscribe(
            (dados) => {
                let TYPED_ARRAY = new Uint8Array(dados.data[0].arquivo.data);
                const STRING_CHAR = String.fromCharCode.apply(null, TYPED_ARRAY);
                let base64String = btoa(STRING_CHAR);
                let url = base64String.split("dataimage/pngbase64")[1];

                // this.myUrl = this.domSanitizer.bypassSecurityTrustUrl('data:image/jpg;base64, ' + base64String);
                this.myUrl = this.domSanitizer.bypassSecurityTrustUrl('data:image/png;base64,'+url);
                // this.myUrl = "data:image/png;base64,";
            },
            (e) => {
                console.log(e);
            }
        );
    }
}